
import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { MatTableFilter } from 'mat-table-filter';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import * as XLSX from 'xlsx';
import { ReportService } from 'app/shared/services/report/report.service';
import { FormBuilder, FormGroup } from '@angular/forms';
import { DatePipe } from '@angular/common';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';
import { UtilityService } from 'app/shared/services/utility/utility.service';


export class RefundPaymentReport {
  TransactionId: string;
  Amount: string;
  ChargerType: string;
  payment_mode: string;
  charger_transaction_id: string;
  Date: string;
  dateConvert: string;
  price_per_unit: string;
  energy_consumped: string;
  Status: string;
}
/**
 * @title Data table with sorting, pagination, and filtering.
 */
@Component({
  selector: 'refund-payment-report',
  templateUrl: './refund-payment-report.component.html',
  styleUrls: ['./refund-payment-report.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations,
  providers: [DatePipe]
})


export class RefundPaymentReportComponent implements OnInit {

  filterEntity: RefundPaymentReport;
  @ViewChild('energyPaginator') energyPaginator: MatPaginator;
  @ViewChild('energySort') energySort: MatSort;
  filterType: MatTableFilter;
  localStorage: any;
  form: FormGroup;
  displayedRfidColumns: string[] = ['TransactionId', 'ChargerType', 'payment_mode', 'charger_transaction_id', 'Status', 'Amount','dateConvert'];
  head = [['Transaction Id', 'Charger Type', 'Payment Mode', 'Charger Transaction Id', 'Status', 'Amount']];
  @ViewChild('rfidPaginator') rfidPaginator: MatPaginator;
  @ViewChild('rfidSort') rfidSort: MatSort;
  transIdSearch = false;
  payModeSearch = false;
  charTransIdSearch = false;
  chargerTypeSearch = false;
  amountSearch = false;
  dateConvertSearch = false;
  statusSearch = false;
  tempDate = new Date();
  exportedData: any[];
  energyCounsumedData: any;
  refundPayment: RefundPaymentReport[];
  dataSource: MatTableDataSource<RefundPaymentReport>;
  currDate: any = new Date();
  menuArray: any;
  constructor(private snackBar: MatSnackBar, private reportservice: ReportService, private fb: FormBuilder, public datepipe: DatePipe,
    private cdr: ChangeDetectorRef,private _utilityService:UtilityService) {

  }
  ngAfterViewInit(){
    this._utilityService.activeMenuItems$.subscribe(menuItemSelecte=> {
      this.menuArray = menuItemSelecte;
      if(this.menuArray == null || this.menuArray == undefined || this.menuArray.title == null || this.menuArray.title == undefined){
        this.menuArray  = JSON.parse( localStorage.getItem('activeMenuItems'));
      }
      this.cdr.detectChanges();
    })
     
  }

  ngOnInit() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));

    this.filterEntity = new RefundPaymentReport();
    this.filterType = MatTableFilter.ANYWHERE;
    this.form = this.fb.group({
      name: ['',],
      toDate: [new Date()],
      fromDate: [new Date(this.tempDate.setDate(this.tempDate.getDate() - 2))]
    });
    this.getRefundTransaction();
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getRefundTransaction() {
    let formValues = this.form.value;
    let params = {
      station_id: 2918,
      f_date: this.datepipe.transform(formValues.fromDate, "yyyy-MM-dd"),//this.dateService.convertToDDMMYYYY(formValues.fromDate),//!!formValues.fromDate ? this.dateService.convertToYYYYMMDD(formValues.fromDate._d) : '',
      t_date: this.datepipe.transform(formValues.toDate, "yyyy-MM-dd"), //this.dateService.convertToDDMMYYYY(formValues.toDate)
      status:"R"
    }
    this.reportservice.getPendingTransactionCW(this.localStorage.userId, params).subscribe(
      (resp: any) => {
        if (resp.status == true) {
          this.refundPayment = resp.data;
          for (var i = 0; i < this.refundPayment.length; i++) {
            this.refundPayment[i].dateConvert = this.datepipe.transform(this.refundPayment[i].Date, 'dd/MM/yyyy hh:mm:ss');
          }
          this.dataSource = new MatTableDataSource(this.refundPayment);
          //this.dataSource.data = this.getGroups(this.allData, this.groupByColumns);
          this.dataSource.paginator = this.rfidPaginator;
          this.dataSource.sort = this.rfidSort;
        } else {
          this.snackBar.open(resp.err_code, 'Dismiss', { duration: 3000 })
          return;
        }
      }, (err) => { }
    )
  }


  changeRfidVisibility(visibleOption, ev) {
    if (visibleOption == 'transIdSearch') {
      this.transIdSearch = !this.transIdSearch;
      this.filterEntity.TransactionId = "";
    }

    if (visibleOption == 'chargerTypeSearch') {
      this.chargerTypeSearch = !this.chargerTypeSearch;
      this.filterEntity.ChargerType = "";

    }
    if (visibleOption == 'amountSearch') {
      this.amountSearch = !this.amountSearch;
      this.filterEntity.Amount = "";

    }
    if (visibleOption == 'dateConvertSearch') {
      this.dateConvertSearch = !this.dateConvertSearch;
      this.filterEntity.dateConvert = '';
    }
    if (visibleOption == 'charTransIdSearch') {
      this.charTransIdSearch = !this.charTransIdSearch;
      this.filterEntity.charger_transaction_id = '';
    }
    if (visibleOption == 'payModeSearch') {
      this.payModeSearch = !this.payModeSearch;
      this.filterEntity.payment_mode = '';
    }
    if (visibleOption == 'statusSearch') {
      this.statusSearch = !this.statusSearch;
      this.filterEntity.Status = '';
    }
  }

  tableExport() {
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var refundpay = this.dataSource.filteredData[i];
      //this.exportedData.push({name: `name${i}`, description: `description${i}`,
      //statusString: `statusString${i}`});
      var data = {
        'TransactionId': refundpay.TransactionId, 'Charger Type': refundpay.ChargerType, 'Payment Mode': refundpay.payment_mode,
        'Charger Trans Id': refundpay.charger_transaction_id, 'Status': refundpay.Status, 'Amount': refundpay.Amount
      };
      this.exportedData.push(data);
    }
    const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
    const workBook: XLSX.WorkBook = XLSX.utils.book_new();

    XLSX.utils.book_append_sheet(workBook, workSheet, 'Refund Transaction Report');
    XLSX.writeFile(workBook, 'Refund Transaction Report.xlsx');

  }

  pdfExport() {
    var doc = new jsPDF({ // set needed dimensions for any elemen

    });
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var refundpay = this.dataSource.filteredData[i];
      //this.exportedData.push({name: `name${i}`, description: `description${i}`,
      //statusString: `statusString${i}`});
      var data = [refundpay.TransactionId, refundpay.ChargerType, refundpay.payment_mode, refundpay.charger_transaction_id, 
            refundpay.Status, refundpay.Amount];

      this.exportedData.push(data);
    }
    var img = new Image();
    var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
    imgRight.src = srcSparkEV;
    var createdDate = new Date().toLocaleString(('en-GB'));
    img.src = src;
    doc.setFontSize(11);
    doc.setTextColor(100);
    doc.setFontSize(18);

    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla', fillColor: [11, 148, 68] },
      styles: {
        cellPadding: 1,
        fontSize: 8,

      },

      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage: data => {
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);

        doc.text('Refund Transaction Report', doc.internal.pageSize.getWidth() / 2, 11, { align: 'center' });

        doc.setTextColor(211, 211, 211);
        doc.setFontSize(60);
        doc.setGState(doc.GState({ opacity: 0.6 }));
        doc.text('Confidential/Not for Public use', 25, 285, null, 60,);
        doc.setGState(doc.GState({ opacity: 1 }));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:' + createdDate, 15, 18);

        doc.text('Generated By:' + this.localStorage.f_Name, 195, 18, { align: 'right' });
        doc = this.addWaterMark(doc);
      }
    })

    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow');
    // below line for Download PDF document  
    doc.save('Refund Transaction report.pdf');

  }
  addWaterMark(doc) {
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
    doc.setFontSize(10);
    //Print Page 1 of 4 for example

    doc.text('Page ' + String(i) + ' of ' + String(newpage), 210 - 20, 297 - 10, null, null, "right");
    return doc;

  }

}

