
import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SuccessfulPaymentReportComponent } from './successful-payment-report/successful-payment-report.component';
import { CanclePaymentReportComponent } from './cancle-payment-report/cancle-payment-report.component';
import { PendingPaymentReportComponent } from './pending-payment-report/pending-payment-report.component';
import { RefundPaymentReportComponent } from './refund-payment-report/refund-payment-report.component'
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'successfulPaymentReports',component : SuccessfulPaymentReportComponent,canActivate: [AuthGuard]
    },
    {
        path : 'cancelPaymentReports',component : CanclePaymentReportComponent,canActivate: [AuthGuard]
    },
    {
        path : 'pendingPaymentReports',component : PendingPaymentReportComponent,canActivate: [AuthGuard]
    },
    {
        path : 'refundPaymentReports',component : RefundPaymentReportComponent,canActivate: [AuthGuard]
    },
    
    
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class PaymentReportsRoutingModule{}